import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Box,
  Chip
} from '@mui/material';
import {
  CloudUpload,
  QueueMusic,
  Favorite,
  PlayArrow
} from '@mui/icons-material';
import axios from 'axios';
import { useAuth } from '../contexts/AuthContext';

const Dashboard = () => {
  const { user } = useAuth();
  const [stats, setStats] = useState({
    tracksUploaded: 0,
    playlistsCreated: 0,
    totalPlays: 0,
    favorites: 0
  });
  const [recentTracks, setRecentTracks] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      // Fetch user profile (includes stats)
      const profileResponse = await axios.get('/api/users/profile');
      const userData = profileResponse.data.user;
      
      // Fetch recent uploads
      const tracksResponse = await axios.get('/api/tracks?limit=5');
      
      // Fetch favorites
      const favoritesResponse = await axios.get('/api/users/favorites');
      
      setStats({
        tracksUploaded: userData.tracks_uploaded || 0,
        playlistsCreated: userData.playlists_created || 0,
        totalPlays: 0, // Calculate from tracks
        favorites: favoritesResponse.data.favorites?.length || 0
      });
      
      setRecentTracks(tracksResponse.data.tracks || []);
    } catch (error) {
      console.error('Error fetching dashboard data:', error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <Container sx={{ py: 4 }}>
        <Typography>Loading dashboard...</Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4, mb: 10 }}>
      <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
        Welcome back, {user?.username}!
      </Typography>
      
      <Typography variant="body1" color="text.secondary" paragraph>
        Here's your OpenStream activity overview
      </Typography>

      {/* Stats Cards */}
      <Grid container spacing={3} sx={{ mb: 6 }}>
        <Grid item xs={12} sm={6} md={3}>
          <Card sx={{ backgroundColor: '#1e1e1e', textAlign: 'center' }}>
            <CardContent>
              <CloudUpload sx={{ fontSize: 40, color: '#1DB954', mb: 1 }} />
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                {stats.tracksUploaded}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Tracks Uploaded
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <Card sx={{ backgroundColor: '#1e1e1e', textAlign: 'center' }}>
            <CardContent>
              <QueueMusic sx={{ fontSize: 40, color: '#1DB954', mb: 1 }} />
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                {stats.playlistsCreated}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Playlists Created
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <Card sx={{ backgroundColor: '#1e1e1e', textAlign: 'center' }}>
            <CardContent>
              <PlayArrow sx={{ fontSize: 40, color: '#1DB954', mb: 1 }} />
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                {stats.totalPlays}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Total Plays
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        
        <Grid item xs={12} sm={6} md={3}>
          <Card sx={{ backgroundColor: '#1e1e1e', textAlign: 'center' }}>
            <CardContent>
              <Favorite sx={{ fontSize: 40, color: '#1DB954', mb: 1 }} />
              <Typography variant="h4" sx={{ fontWeight: 'bold' }}>
                {stats.favorites}
              </Typography>
              <Typography variant="body2" color="text.secondary">
                Favorites
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Recent Activity */}
      <Grid container spacing={4}>
        <Grid item xs={12} md={8}>
          <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
            Recent Tracks
          </Typography>
          
          {recentTracks.length > 0 ? (
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              {recentTracks.map((track) => (
                <Card key={track.id} sx={{ backgroundColor: '#1e1e1e' }}>
                  <CardContent>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'start' }}>
                      <Box>
                        <Typography variant="h6">{track.title}</Typography>
                        <Typography variant="body2" color="text.secondary">
                          {track.artist} • {track.album}
                        </Typography>
                        <Box sx={{ mt: 1 }}>
                          {track.genre && (
                            <Chip label={track.genre} size="small" variant="outlined" />
                          )}
                        </Box>
                      </Box>
                      <Box sx={{ textAlign: 'right' }}>
                        <Typography variant="body2" color="text.secondary">
                          {track.play_count || 0} plays
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          {new Date(track.created_at).toLocaleDateString()}
                        </Typography>
                      </Box>
                    </Box>
                  </CardContent>
                </Card>
              ))}
            </Box>
          ) : (
            <Card sx={{ backgroundColor: '#1e1e1e' }}>
              <CardContent>
                <Typography variant="body1" color="text.secondary" sx={{ textAlign: 'center', py: 4 }}>
                  No tracks uploaded yet. Start by uploading your first track!
                </Typography>
              </CardContent>
            </Card>
          )}
        </Grid>

        <Grid item xs={12} md={4}>
          <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
            Quick Actions
          </Typography>
          
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
            <Card sx={{ backgroundColor: '#1e1e1e', cursor: 'pointer', '&:hover': { backgroundColor: '#2a2a2a' } }}>
              <CardContent>
                <CloudUpload sx={{ color: '#1DB954', mb: 1 }} />
                <Typography variant="h6">Upload Track</Typography>
                <Typography variant="body2" color="text.secondary">
                  Share your music with the world
                </Typography>
              </CardContent>
            </Card>
            
            <Card sx={{ backgroundColor: '#1e1e1e', cursor: 'pointer', '&:hover': { backgroundColor: '#2a2a2a' } }}>
              <CardContent>
                <QueueMusic sx={{ color: '#1DB954', mb: 1 }} />
                <Typography variant="h6">Create Playlist</Typography>
                <Typography variant="body2" color="text.secondary">
                  Organize your favorite tracks
                </Typography>
              </CardContent>
            </Card>
          </Box>
        </Grid>
      </Grid>
    </Container>
  );
};

export default Dashboard;
