import React, { useState, useEffect } from 'react';
import {
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Box,
  Tabs,
  Tab,
  IconButton,
  Chip
} from '@mui/material';
import { PlayArrow, Person, Album, MusicNote } from '@mui/icons-material';
import { useSearchParams } from 'react-router-dom';
import axios from 'axios';
import { usePlayer } from '../contexts/PlayerContext';

const Search = () => {
  const [searchParams] = useSearchParams();
  const query = searchParams.get('q');
  const [results, setResults] = useState({});
  const [loading, setLoading] = useState(false);
  const [activeTab, setActiveTab] = useState(0);
  const { playTrack } = usePlayer();

  useEffect(() => {
    if (query) {
      performSearch(query);
    }
  }, [query]);

  const performSearch = async (searchQuery) => {
    setLoading(true);
    try {
      const response = await axios.get(`/api/search?q=${encodeURIComponent(searchQuery)}`);
      setResults(response.data.results);
    } catch (error) {
      console.error('Search error:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const handlePlayTrack = (track) => {
    const trackList = results.tracks || [];
    playTrack(track, trackList);
  };

  if (!query) {
    return (
      <Container sx={{ py: 4, textAlign: 'center' }}>
        <Typography variant="h5" color="text.secondary">
          Enter a search term to find music
        </Typography>
      </Container>
    );
  }

  if (loading) {
    return (
      <Container sx={{ py: 4 }}>
        <Typography>Searching for "{query}"...</Typography>
      </Container>
    );
  }

  const tabs = [
    { label: 'All', icon: <MusicNote /> },
    { label: `Tracks (${results.tracks?.length || 0})`, icon: <MusicNote /> },
    { label: `Artists (${results.artists?.length || 0})`, icon: <Person /> },
    { label: `Albums (${results.albums?.length || 0})`, icon: <Album /> }
  ];

  return (
    <Container maxWidth="lg" sx={{ py: 4, mb: 10 }}>
      <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
        Search Results for "{query}"
      </Typography>

      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
        <Tabs value={activeTab} onChange={handleTabChange}>
          {tabs.map((tab, index) => (
            <Tab key={index} label={tab.label} icon={tab.icon} iconPosition="start" />
          ))}
        </Tabs>
      </Box>

      {/* All Results */}
      {activeTab === 0 && (
        <Box>
          {/* Top Tracks */}
          {results.tracks && results.tracks.length > 0 && (
            <Box sx={{ mb: 4 }}>
              <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
                Tracks
              </Typography>
              <Grid container spacing={2}>
                {results.tracks.slice(0, 6).map((track) => (
                  <Grid item xs={12} sm={6} md={4} key={track.id}>
                    <Card sx={{ backgroundColor: '#1e1e1e' }}>
                      <CardContent>
                        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'start' }}>
                          <Box sx={{ flexGrow: 1 }}>
                            <Typography variant="h6" noWrap>{track.title}</Typography>
                            <Typography variant="body2" color="text.secondary" noWrap>
                              {track.artist}
                            </Typography>
                            {track.genre && (
                              <Chip label={track.genre} size="small" variant="outlined" sx={{ mt: 1 }} />
                            )}
                          </Box>
                          <IconButton onClick={() => handlePlayTrack(track)}>
                            <PlayArrow />
                          </IconButton>
                        </Box>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            </Box>
          )}

          {/* Top Artists */}
          {results.artists && results.artists.length > 0 && (
            <Box sx={{ mb: 4 }}>
              <Typography variant="h5" gutterBottom sx={{ fontWeight: 'bold' }}>
                Artists
              </Typography>
              <Grid container spacing={2}>
                {results.artists.slice(0, 4).map((artist, index) => (
                  <Grid item xs={12} sm={6} md={3} key={index}>
                    <Card sx={{ backgroundColor: '#1e1e1e', textAlign: 'center' }}>
                      <CardContent>
                        <Person sx={{ fontSize: 48, color: '#1DB954', mb: 1 }} />
                        <Typography variant="h6">{artist.artist}</Typography>
                        <Typography variant="body2" color="text.secondary">
                          {artist.track_count} tracks • {artist.total_plays} plays
                        </Typography>
                      </CardContent>
                    </Card>
                  </Grid>
                ))}
              </Grid>
            </Box>
          )}
        </Box>
      )}

      {/* Tracks Tab */}
      {activeTab === 1 && (
        <Grid container spacing={3}>
          {results.tracks && results.tracks.length > 0 ? (
            results.tracks.map((track) => (
              <Grid item xs={12} sm={6} md={4} key={track.id}>
                <Card sx={{ backgroundColor: '#1e1e1e' }}>
                  <CardContent>
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'start' }}>
                      <Box sx={{ flexGrow: 1 }}>
                        <Typography variant="h6" noWrap>{track.title}</Typography>
                        <Typography variant="body2" color="text.secondary" noWrap>
                          {track.artist}
                        </Typography>
                        {track.album && (
                          <Typography variant="body2" color="text.secondary" noWrap>
                            {track.album}
                          </Typography>
                        )}
                        {track.genre && (
                          <Chip label={track.genre} size="small" variant="outlined" sx={{ mt: 1 }} />
                        )}
                        <Typography variant="caption" color="text.secondary" sx={{ display: 'block', mt: 1 }}>
                          {track.play_count || 0} plays
                        </Typography>
                      </Box>
                      <IconButton onClick={() => handlePlayTrack(track)}>
                        <PlayArrow />
                      </IconButton>
                    </Box>
                  </CardContent>
                </Card>
              </Grid>
            ))
          ) : (
            <Grid item xs={12}>
              <Typography variant="body1" color="text.secondary" sx={{ textAlign: 'center' }}>
                No tracks found for "{query}"
              </Typography>
            </Grid>
          )}
        </Grid>
      )}

      {/* Artists Tab */}
      {activeTab === 2 && (
        <Grid container spacing={3}>
          {results.artists && results.artists.length > 0 ? (
            results.artists.map((artist, index) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <Card sx={{ backgroundColor: '#1e1e1e', textAlign: 'center' }}>
                  <CardContent>
                    <Person sx={{ fontSize: 64, color: '#1DB954', mb: 2 }} />
                    <Typography variant="h5">{artist.artist}</Typography>
                    <Typography variant="body2" color="text.secondary">
                      {artist.track_count} tracks
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {artist.total_plays} total plays
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))
          ) : (
            <Grid item xs={12}>
              <Typography variant="body1" color="text.secondary" sx={{ textAlign: 'center' }}>
                No artists found for "{query}"
              </Typography>
            </Grid>
          )}
        </Grid>
      )}

      {/* Albums Tab */}
      {activeTab === 3 && (
        <Grid container spacing={3}>
          {results.albums && results.albums.length > 0 ? (
            results.albums.map((album, index) => (
              <Grid item xs={12} sm={6} md={4} key={index}>
                <Card sx={{ backgroundColor: '#1e1e1e' }}>
                  <CardContent>
                    <Album sx={{ fontSize: 48, color: '#1DB954', mb: 2 }} />
                    <Typography variant="h6">{album.album}</Typography>
                    <Typography variant="body2" color="text.secondary">
                      by {album.artist}
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      {album.track_count} tracks • {album.total_plays} plays
                    </Typography>
                  </CardContent>
                </Card>
              </Grid>
            ))
          ) : (
            <Grid item xs={12}>
              <Typography variant="body1" color="text.secondary" sx={{ textAlign: 'center' }}>
                No albums found for "{query}"
              </Typography>
            </Grid>
          )}
        </Grid>
      )}
    </Container>
  );
};

export default Search;
