import React, { useState, useEffect, useRef } from 'react';
import {
  Box,
  Paper,
  Typography,
  IconButton,
  Slider,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Chip,
  Dialog,
  DialogTitle,
  DialogContent,
  Button,
  TextField,
  Tooltip
} from '@mui/material';
import {
  PlayArrow,
  Pause,
  SkipNext,
  SkipPrevious,
  VolumeUp,
  QueueMusic,
  Add,
  MusicNote
} from '@mui/icons-material';

const RoomAudioPlayer = ({ room, isHost, onAddTrack, tracks = [] }) => {
  const [isPlaying, setIsPlaying] = useState(false);
  const [currentTime, setCurrentTime] = useState(0);
  const [duration, setDuration] = useState(0);
  const [volume, setVolume] = useState(0.7);
  const [currentTrack, setCurrentTrack] = useState(null);
  const [showAddDialog, setShowAddDialog] = useState(false);
  const [newTrackTitle, setNewTrackTitle] = useState('');
  const [newTrackArtist, setNewTrackArtist] = useState('');
  const [newTrackUrl, setNewTrackUrl] = useState('');
  const audioRef = useRef(null);

  useEffect(() => {
    if (tracks.length > 0 && !currentTrack) {
      setCurrentTrack(tracks[0]);
    }
  }, [tracks, currentTrack]);

  useEffect(() => {
    const audio = audioRef.current;
    if (!audio) return;

    const updateTime = () => setCurrentTime(audio.currentTime);
    const updateDuration = () => setDuration(audio.duration);
    const handleEnded = () => {
      setIsPlaying(false);
      // Auto-play next track
      if (currentTrack && tracks.length > 1) {
        const currentIndex = tracks.findIndex(t => t.id === currentTrack.id);
        const nextIndex = (currentIndex + 1) % tracks.length;
        setCurrentTrack(tracks[nextIndex]);
      }
    };

    audio.addEventListener('timeupdate', updateTime);
    audio.addEventListener('loadedmetadata', updateDuration);
    audio.addEventListener('ended', handleEnded);

    return () => {
      audio.removeEventListener('timeupdate', updateTime);
      audio.removeEventListener('loadedmetadata', updateDuration);
      audio.removeEventListener('ended', handleEnded);
    };
  }, [currentTrack, tracks]);

  const handlePlayPause = () => {
    const audio = audioRef.current;
    if (isPlaying) {
      audio.pause();
    } else {
      audio.play();
    }
    setIsPlaying(!isPlaying);
  };

  const handleNext = () => {
    if (currentTrack && tracks.length > 1) {
      const currentIndex = tracks.findIndex(t => t.id === currentTrack.id);
      const nextIndex = (currentIndex + 1) % tracks.length;
      setCurrentTrack(tracks[nextIndex]);
    }
  };

  const handlePrevious = () => {
    if (currentTrack && tracks.length > 1) {
      const currentIndex = tracks.findIndex(t => t.id === currentTrack.id);
      const prevIndex = currentIndex === 0 ? tracks.length - 1 : currentIndex - 1;
      setCurrentTrack(tracks[prevIndex]);
    }
  };

  const handleSeek = (event, newValue) => {
    const audio = audioRef.current;
    if (audio) {
      audio.currentTime = newValue;
      setCurrentTime(newValue);
    }
  };

  const handleVolumeChange = (event, newValue) => {
    setVolume(newValue);
    const audio = audioRef.current;
    if (audio) {
      audio.volume = newValue;
    }
  };

  const formatTime = (seconds) => {
    if (isNaN(seconds)) return '0:00';
    const mins = Math.floor(seconds / 60);
    const secs = Math.floor(seconds % 60);
    return `${mins}:${secs.toString().padStart(2, '0')}`;
  };

  const handleAddTrack = async () => {
    if (newTrackTitle.trim() && newTrackArtist.trim() && newTrackUrl.trim()) {
      const trackData = {
        title: newTrackTitle.trim(),
        artist: newTrackArtist.trim(),
        url: newTrackUrl.trim()
      };
      
      if (onAddTrack) {
        await onAddTrack(trackData);
      }
      
      // Reset form
      setNewTrackTitle('');
      setNewTrackArtist('');
      setNewTrackUrl('');
      setShowAddDialog(false);
    }
  };

  if (!currentTrack && tracks.length === 0) {
    return (
      <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
        <Box sx={{ textAlign: 'center', py: 4 }}>
          <MusicNote sx={{ fontSize: 64, color: 'text.secondary', mb: 2 }} />
          <Typography variant="h6" color="text.secondary" gutterBottom>
            No tracks in the queue
          </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>
            {isHost ? 'Add some tracks to get the party started!' : 'Waiting for the host to add tracks...'}
          </Typography>
          {isHost && (
            <Button
              variant="contained"
              startIcon={<Add />}
              onClick={() => setShowAddDialog(true)}
            >
              Add Track
            </Button>
          )}
        </Box>
      </Paper>
    );
  }

  return (
    <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
      {/* Audio element */}
      {currentTrack && (
        <audio
          ref={audioRef}
          src={currentTrack.url}
          volume={volume}
          onLoadStart={() => setIsPlaying(false)}
        />
      )}

      {/* Current Track Info */}
      {currentTrack && (
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
          <Avatar sx={{ width: 64, height: 64, mr: 2, bgcolor: 'primary.main' }}>
            <MusicNote />
          </Avatar>
          <Box sx={{ flex: 1 }}>
            <Typography variant="h6" component="div">
              {currentTrack.title}
            </Typography>
            <Typography variant="subtitle1" color="text.secondary">
              {currentTrack.artist}
            </Typography>
            <Chip 
              label="Now Playing" 
              color="primary" 
              size="small" 
              sx={{ mt: 0.5 }}
            />
          </Box>
        </Box>
      )}

      {/* Player Controls */}
      <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'center', mb: 2 }}>
        <IconButton 
          onClick={handlePrevious} 
          disabled={!currentTrack || tracks.length <= 1}
        >
          <SkipPrevious />
        </IconButton>
        
        <Tooltip title={isPlaying ? "Pause" : "Play"}>
          <IconButton 
            onClick={handlePlayPause} 
            disabled={!currentTrack}
            sx={{ mx: 1, bgcolor: 'primary.main', color: 'white', '&:hover': { bgcolor: 'primary.dark' } }}
          >
            {isPlaying ? <Pause /> : <PlayArrow />}
          </IconButton>
        </Tooltip>
        
        <IconButton 
          onClick={handleNext} 
          disabled={!currentTrack || tracks.length <= 1}
        >
          <SkipNext />
        </IconButton>
      </Box>

      {/* Progress Bar */}
      {currentTrack && (
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <Typography variant="body2" sx={{ minWidth: 40 }}>
            {formatTime(currentTime)}
          </Typography>
          <Slider
            size="small"
            value={currentTime}
            max={duration || 100}
            onChange={handleSeek}
            sx={{ mx: 2 }}
          />
          <Typography variant="body2" sx={{ minWidth: 40 }}>
            {formatTime(duration)}
          </Typography>
        </Box>
      )}

      {/* Volume Control */}
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 3 }}>
        <VolumeUp sx={{ mr: 1 }} />
        <Slider
          size="small"
          value={volume}
          min={0}
          max={1}
          step={0.1}
          onChange={handleVolumeChange}
          sx={{ width: 100 }}
        />
      </Box>

      {/* Queue/Playlist */}
      {tracks.length > 0 && (
        <Box>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
            <Typography variant="h6">
              Queue ({tracks.length} tracks)
            </Typography>
            {isHost && (
              <Button
                variant="outlined"
                size="small"
                startIcon={<Add />}
                onClick={() => setShowAddDialog(true)}
              >
                Add Track
              </Button>
            )}
          </Box>
          
          <List sx={{ maxHeight: 300, overflow: 'auto' }}>
            {tracks.map((track, index) => (
              <ListItem
                key={track.id}
                button
                onClick={() => setCurrentTrack(track)}
                selected={currentTrack?.id === track.id}
                sx={{
                  borderRadius: 1,
                  mb: 0.5,
                  '&.Mui-selected': {
                    bgcolor: 'primary.light',
                    color: 'primary.contrastText'
                  }
                }}
              >
                <ListItemAvatar>
                  <Avatar sx={{ bgcolor: currentTrack?.id === track.id ? 'primary.main' : 'grey.300' }}>
                    {currentTrack?.id === track.id ? <QueueMusic /> : index + 1}
                  </Avatar>
                </ListItemAvatar>
                <ListItemText
                  primary={track.title}
                  secondary={track.artist}
                  primaryTypographyProps={{
                    fontWeight: currentTrack?.id === track.id ? 'bold' : 'normal'
                  }}
                />
              </ListItem>
            ))}
          </List>
        </Box>
      )}

      {/* Add Track Dialog */}
      <Dialog open={showAddDialog} onClose={() => setShowAddDialog(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Add Track to Queue</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label="Track Title"
            fullWidth
            variant="outlined"
            value={newTrackTitle}
            onChange={(e) => setNewTrackTitle(e.target.value)}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Artist"
            fullWidth
            variant="outlined"
            value={newTrackArtist}
            onChange={(e) => setNewTrackArtist(e.target.value)}
            sx={{ mb: 2 }}
          />
          <TextField
            margin="dense"
            label="Audio URL"
            fullWidth
            variant="outlined"
            value={newTrackUrl}
            onChange={(e) => setNewTrackUrl(e.target.value)}
            sx={{ mb: 2 }}
          />
          <Box sx={{ display: 'flex', justifyContent: 'flex-end', gap: 1, mt: 2 }}>
            <Button onClick={() => setShowAddDialog(false)}>
              Cancel
            </Button>
            <Button 
              onClick={handleAddTrack} 
              variant="contained"
              disabled={!newTrackTitle.trim() || !newTrackArtist.trim() || !newTrackUrl.trim()}
            >
              Add Track
            </Button>
          </Box>
        </DialogContent>
      </Dialog>
    </Paper>
  );
};

export default RoomAudioPlayer;
