import React, { useState } from 'react';
import {
  Card,
  CardContent,
  CardActions,
  Typography,
  Box,
  Chip,
  Avatar,
  IconButton,
  Button,
  Tooltip,
  Badge,
  LinearProgress,
  Stack,
  Fade,
  Zoom
} from '@mui/material';
import {
  Groups,
  Star,
  StarBorder,
  Share,
  Bookmark,
  BookmarkBorder,
  Schedule,
  Language,
  VolumeUp,
  PanTool,
  Visibility,
  AccessTime,
  TrendingUp,
  AutoAwesome,
  LiveTv
} from '@mui/icons-material';

const EnhancedRoomCard = ({ 
  room, 
  onJoin, 
  onFollow, 
  onShare, 
  isFollowing = false,
  showAnalytics = false 
}) => {
  const [isHovered, setIsHovered] = useState(false);
  const [showDetails, setShowDetails] = useState(false);

  const getCategoryIcon = (category) => {
    const icons = {
      music: '🎵',
      business: '💼',
      education: '📚',
      gaming: '🎮',
      entertainment: '🎬',
      wellness: '🧘',
      fitness: '💪',
      social: '👥'
    };
    return icons[category] || '💬';
  };

  const getMoodColor = (mood) => {
    const colors = {
      energetic: '#ff4757',
      chill: '#3742fa',
      focused: '#2ed573',
      creative: '#ff6348',
      social: '#ffa502',
      learning: '#5352ed',
      supportive: '#ff9ff3'
    };
    return colors[mood] || '#1DB954';
  };

  const getThemeGradient = (theme) => {
    const gradients = {
      sunset: 'linear-gradient(135deg, #ff9a9e 0%, #fecfef 100%)',
      ocean: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      forest: 'linear-gradient(135deg, #134e5e 0%, #71b280 100%)',
      cosmic: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      neon: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
      default: 'linear-gradient(135deg, #1DB954 0%, #1ed760 100%)'
    };
    return gradients[theme] || gradients.default;
  };

  const formatTimeAgo = (timestamp) => {
    const now = new Date();
    const created = new Date(timestamp);
    const diffMs = now - created;
    const diffMins = Math.floor(diffMs / 60000);
    
    if (diffMins < 60) return `${diffMins}m ago`;
    if (diffMins < 1440) return `${Math.floor(diffMins / 60)}h ago`;
    return `${Math.floor(diffMins / 1440)}d ago`;
  };

  const isScheduled = room.scheduled_start && new Date(room.scheduled_start) > new Date();
  const isLive = room.is_active && !isScheduled;
  const occupancyRate = (room.participant_count / room.max_participants) * 100;

  return (
    <Zoom in timeout={300}>
      <Card
        onMouseEnter={() => setIsHovered(true)}
        onMouseLeave={() => setIsHovered(false)}
        sx={{
          height: '100%',
          position: 'relative',
          background: getThemeGradient(room.background_theme || 'default'),
          border: `2px solid ${isLive ? '#ff4757' : 'rgba(255,255,255,0.1)'}`,
          borderRadius: 4,
          overflow: 'hidden',
          cursor: 'pointer',
          transition: 'all 0.3s cubic-bezier(0.4, 0, 0.2, 1)',
          transform: isHovered ? 'translateY(-8px) scale(1.02)' : 'translateY(0) scale(1)',
          boxShadow: isHovered 
            ? '0 20px 40px rgba(0,0,0,0.4), 0 0 20px rgba(29, 185, 84, 0.3)'
            : '0 8px 25px rgba(0,0,0,0.15)',
          '&::before': {
            content: '""',
            position: 'absolute',
            top: 0,
            left: 0,
            right: 0,
            bottom: 0,
            background: 'rgba(0,0,0,0.2)',
            zIndex: 1
          }
        }}
        onClick={() => onJoin(room.id)}
      >
        {/* Live/Scheduled Badge */}
        <Box sx={{ position: 'absolute', top: 12, left: 12, zIndex: 3 }}>
          {isLive && (
            <Chip
              icon={<LiveTv />}
              label="LIVE"
              size="small"
              sx={{
                background: 'rgba(255, 71, 87, 0.9)',
                color: 'white',
                fontWeight: 'bold',
                animation: 'pulse 2s infinite'
              }}
            />
          )}
          {isScheduled && (
            <Chip
              icon={<Schedule />}
              label="Scheduled"
              size="small"
              sx={{
                background: 'rgba(53, 82, 237, 0.9)',
                color: 'white',
                fontWeight: 'bold'
              }}
            />
          )}
        </Box>

        {/* Featured Badge */}
        {room.featured && (
          <Box sx={{ position: 'absolute', top: 12, right: 12, zIndex: 3 }}>
            <Tooltip title="Featured Room">
              <Avatar sx={{ 
                width: 32, 
                height: 32, 
                background: 'linear-gradient(45deg, #FFD700, #FFA500)',
                animation: 'sparkle 3s infinite'
              }}>
                <AutoAwesome sx={{ fontSize: 18 }} />
              </Avatar>
            </Tooltip>
          </Box>
        )}

        <CardContent sx={{ position: 'relative', zIndex: 2, color: 'white', pb: 1 }}>
          {/* Room Header */}
          <Box sx={{ display: 'flex', alignItems: 'flex-start', mb: 2 }}>
            <Avatar 
              sx={{ 
                mr: 2, 
                width: 50, 
                height: 50,
                background: 'rgba(255,255,255,0.2)',
                backdropFilter: 'blur(10px)',
                fontSize: '1.5rem'
              }}
            >
              {getCategoryIcon(room.category)}
            </Avatar>
            <Box sx={{ flexGrow: 1, minWidth: 0 }}>
              <Typography 
                variant="h6" 
                sx={{ 
                  fontWeight: 'bold',
                  mb: 0.5,
                  textShadow: '2px 2px 4px rgba(0,0,0,0.5)'
                }}
                noWrap
              >
                {room.title}
              </Typography>
              <Typography 
                variant="body2" 
                sx={{ 
                  opacity: 0.9,
                  textShadow: '1px 1px 2px rgba(0,0,0,0.5)'
                }}
              >
                by {room.host_display_name}
              </Typography>
            </Box>
          </Box>

          {/* Room Description */}
          <Typography 
            variant="body2" 
            sx={{ 
              mb: 2, 
              opacity: 0.9,
              textShadow: '1px 1px 2px rgba(0,0,0,0.5)',
              overflow: 'hidden',
              textOverflow: 'ellipsis',
              display: '-webkit-box',
              WebkitLineClamp: 2,
              WebkitBoxOrient: 'vertical'
            }}
          >
            {room.description}
          </Typography>

          {/* Tags */}
          {room.tags && room.tags.length > 0 && (
            <Stack direction="row" spacing={1} sx={{ mb: 2, flexWrap: 'wrap' }}>
              {room.tags.slice(0, 3).map((tag, index) => (
                <Chip
                  key={index}
                  label={tag}
                  size="small"
                  sx={{
                    background: 'rgba(255,255,255,0.2)',
                    color: 'white',
                    fontSize: '0.7rem',
                    height: 20
                  }}
                />
              ))}
              {room.tags.length > 3 && (
                <Chip
                  label={`+${room.tags.length - 3}`}
                  size="small"
                  sx={{
                    background: 'rgba(255,255,255,0.3)',
                    color: 'white',
                    fontSize: '0.7rem',
                    height: 20
                  }}
                />
              )}
            </Stack>
          )}

          {/* Room Stats */}
          <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <Groups sx={{ mr: 1, fontSize: 18 }} />
              <Typography variant="body2" sx={{ fontWeight: 'bold' }}>
                {room.participant_count}/{room.max_participants}
              </Typography>
            </Box>
            
            {room.mood && (
              <Chip
                label={room.mood}
                size="small"
                sx={{
                  background: getMoodColor(room.mood),
                  color: 'white',
                  fontWeight: 'bold',
                  textTransform: 'capitalize'
                }}
              />
            )}

            <Typography variant="caption" sx={{ opacity: 0.8 }}>
              {formatTimeAgo(room.created_at)}
            </Typography>
          </Box>

          {/* Occupancy Bar */}
          <Box sx={{ mb: 2 }}>
            <LinearProgress
              variant="determinate"
              value={occupancyRate}
              sx={{
                height: 6,
                borderRadius: 3,
                backgroundColor: 'rgba(255,255,255,0.2)',
                '& .MuiLinearProgress-bar': {
                  borderRadius: 3,
                  background: occupancyRate > 80 
                    ? 'linear-gradient(45deg, #ff4757, #ff3742)'
                    : occupancyRate > 50
                    ? 'linear-gradient(45deg, #ffa502, #ff6348)'
                    : 'linear-gradient(45deg, #2ed573, #1dd1a1)'
                }
              }}
            />
          </Box>
        </CardContent>

        {/* Action Buttons */}
        <CardActions 
          sx={{ 
            position: 'relative', 
            zIndex: 2, 
            justifyContent: 'space-between',
            background: 'rgba(0,0,0,0.3)',
            backdropFilter: 'blur(10px)'
          }}
        >
          <Box>
            <Tooltip title={isFollowing ? "Unfollow" : "Follow"}>
              <IconButton
                onClick={(e) => {
                  e.stopPropagation();
                  onFollow(room.id);
                }}
                sx={{ color: 'white' }}
              >
                {isFollowing ? <Star /> : <StarBorder />}
              </IconButton>
            </Tooltip>
            
            <Tooltip title="Share Room">
              <IconButton
                onClick={(e) => {
                  e.stopPropagation();
                  onShare(room);
                }}
                sx={{ color: 'white' }}
              >
                <Share />
              </IconButton>
            </Tooltip>
          </Box>

          <Button
            variant="contained"
            onClick={(e) => {
              e.stopPropagation();
              onJoin(room.id);
            }}
            sx={{
              background: 'rgba(255,255,255,0.2)',
              backdropFilter: 'blur(10px)',
              border: '1px solid rgba(255,255,255,0.3)',
              color: 'white',
              fontWeight: 'bold',
              '&:hover': {
                background: 'rgba(255,255,255,0.3)',
                transform: 'scale(1.05)'
              }
            }}
          >
            {isScheduled ? 'Set Reminder' : 'Join'}
          </Button>
        </CardActions>

        {/* Hover Effects */}
        <Fade in={isHovered}>
          <Box
            sx={{
              position: 'absolute',
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              background: 'rgba(29, 185, 84, 0.1)',
              zIndex: 1
            }}
          />
        </Fade>
      </Card>
    </Zoom>
  );
};

export default EnhancedRoomCard;
