import React from 'react';
import {
  Box,
  Paper,
  Typography,
  IconButton,
  Slider,
  LinearProgress
} from '@mui/material';
import {
  PlayArrow,
  Pause,
  SkipNext,
  SkipPrevious,
  VolumeUp,
  VolumeDown
} from '@mui/icons-material';
import { usePlayer } from '../contexts/PlayerContext';

const Player = () => {
  const {
    currentTrack,
    isPlaying,
    volume,
    currentTime,
    duration,
    togglePlayPause,
    seek,
    changeVolume,
    playNext,
    playPrevious
  } = usePlayer();

  if (!currentTrack) {
    return null;
  }

  const formatTime = (time) => {
    const minutes = Math.floor(time / 60);
    const seconds = Math.floor(time % 60);
    return `${minutes}:${seconds.toString().padStart(2, '0')}`;
  };

  const handleSeek = (event, newValue) => {
    seek(newValue);
  };

  const handleVolumeChange = (event, newValue) => {
    changeVolume(newValue / 100);
  };

  const progress = duration > 0 ? (currentTime / duration) * 100 : 0;

  return (
    <Paper
      elevation={8}
      sx={{
        position: 'fixed',
        bottom: 0,
        left: 0,
        right: 0,
        zIndex: 1300,
        backgroundColor: '#181818',
        borderTop: '1px solid #282828',
        p: 2
      }}
    >
      {/* Progress Bar */}
      <LinearProgress
        variant="determinate"
        value={progress}
        sx={{
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          height: 4,
          backgroundColor: 'rgba(255, 255, 255, 0.1)',
          '& .MuiLinearProgress-bar': {
            backgroundColor: '#1DB954'
          }
        }}
      />

      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          mt: 1
        }}
      >
        {/* Track Info */}
        <Box sx={{ display: 'flex', alignItems: 'center', flex: 1 }}>
          <Box sx={{ mr: 2 }}>
            <Typography variant="subtitle1" sx={{ fontWeight: 600 }}>
              {currentTrack.title}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {currentTrack.artist}
            </Typography>
          </Box>
        </Box>

        {/* Player Controls */}
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            gap: 1,
            flex: 1,
            justifyContent: 'center'
          }}
        >
          <IconButton onClick={playPrevious} color="inherit">
            <SkipPrevious />
          </IconButton>
          
          <IconButton
            onClick={togglePlayPause}
            sx={{
              backgroundColor: '#1DB954',
              color: 'white',
              '&:hover': {
                backgroundColor: '#1ed760'
              }
            }}
          >
            {isPlaying ? <Pause /> : <PlayArrow />}
          </IconButton>
          
          <IconButton onClick={playNext} color="inherit">
            <SkipNext />
          </IconButton>

          {/* Time Display */}
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, ml: 2 }}>
            <Typography variant="caption">
              {formatTime(currentTime)}
            </Typography>
            <Slider
              size="small"
              value={currentTime}
              max={duration}
              onChange={handleSeek}
              sx={{
                width: 200,
                color: '#1DB954',
                '& .MuiSlider-thumb': {
                  width: 12,
                  height: 12
                }
              }}
            />
            <Typography variant="caption">
              {formatTime(duration)}
            </Typography>
          </Box>
        </Box>

        {/* Volume Control */}
        <Box
          sx={{
            display: 'flex',
            alignItems: 'center',
            gap: 1,
            flex: 1,
            justifyContent: 'flex-end'
          }}
        >
          <VolumeDown />
          <Slider
            size="small"
            value={volume * 100}
            onChange={handleVolumeChange}
            sx={{
              width: 100,
              color: '#1DB954'
            }}
          />
          <VolumeUp />
        </Box>
      </Box>
    </Paper>
  );
};

export default Player;
