import React, { useState } from 'react';
import {
  Container,
  Typography,
  Paper,
  TextField,
  Button,
  Box,
  Alert,
  LinearProgress,
  Card,
  CardContent
} from '@mui/material';
import { CloudUpload } from '@mui/icons-material';
import axios from 'axios';

const Upload = () => {
  const [formData, setFormData] = useState({
    title: '',
    artist: '',
    album: '',
    genre: ''
  });
  const [file, setFile] = useState(null);
  const [uploading, setUploading] = useState(false);
  const [progress, setProgress] = useState(0);
  const [message, setMessage] = useState('');
  const [error, setError] = useState('');

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleFileChange = (e) => {
    const selectedFile = e.target.files[0];
    if (selectedFile) {
      // Validate file type
      const allowedTypes = ['audio/mpeg', 'audio/wav', 'audio/flac', 'audio/aac', 'audio/ogg'];
      if (!allowedTypes.includes(selectedFile.type)) {
        setError('Please select a valid audio file (MP3, WAV, FLAC, AAC, OGG)');
        return;
      }
      
      // Validate file size (100MB limit)
      if (selectedFile.size > 100 * 1024 * 1024) {
        setError('File size must be less than 100MB');
        return;
      }
      
      setFile(selectedFile);
      setError('');
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!file) {
      setError('Please select an audio file');
      return;
    }

    if (!formData.title || !formData.artist) {
      setError('Title and Artist are required');
      return;
    }

    setUploading(true);
    setError('');
    setMessage('');
    setProgress(0);

    try {
      const uploadFormData = new FormData();
      uploadFormData.append('audio', file);
      uploadFormData.append('title', formData.title);
      uploadFormData.append('artist', formData.artist);
      uploadFormData.append('album', formData.album);
      uploadFormData.append('genre', formData.genre);

      const response = await axios.post('/api/tracks', uploadFormData, {
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        onUploadProgress: (progressEvent) => {
          const percentCompleted = Math.round(
            (progressEvent.loaded * 100) / progressEvent.total
          );
          setProgress(percentCompleted);
        },
      });

      setMessage('Track uploaded successfully! Processing audio...');
      
      // Reset form
      setFormData({
        title: '',
        artist: '',
        album: '',
        genre: ''
      });
      setFile(null);
      
      // Reset file input
      const fileInput = document.getElementById('audio-file');
      if (fileInput) {
        fileInput.value = '';
      }
      
    } catch (error) {
      setError(error.response?.data?.error || 'Upload failed');
    } finally {
      setUploading(false);
      setProgress(0);
    }
  };

  return (
    <Container maxWidth="md" sx={{ py: 4, mb: 10 }}>
      <Typography variant="h4" gutterBottom sx={{ fontWeight: 'bold' }}>
        Upload Track
      </Typography>
      
      <Typography variant="body1" color="text.secondary" paragraph>
        Share your music with the OpenStream community
      </Typography>

      <Paper elevation={3} sx={{ p: 4, backgroundColor: '#1e1e1e' }}>
        {error && (
          <Alert severity="error" sx={{ mb: 3 }}>
            {error}
          </Alert>
        )}
        
        {message && (
          <Alert severity="success" sx={{ mb: 3 }}>
            {message}
          </Alert>
        )}

        {uploading && (
          <Box sx={{ mb: 3 }}>
            <LinearProgress variant="determinate" value={progress} />
            <Typography variant="body2" sx={{ mt: 1 }}>
              Uploading... {progress}%
            </Typography>
          </Box>
        )}

        <Box component="form" onSubmit={handleSubmit}>
          {/* File Upload */}
          <Card sx={{ mb: 3, backgroundColor: '#2a2a2a' }}>
            <CardContent>
              <Box
                sx={{
                  border: '2px dashed #1DB954',
                  borderRadius: 2,
                  p: 4,
                  textAlign: 'center',
                  cursor: 'pointer',
                  '&:hover': {
                    backgroundColor: 'rgba(29, 185, 84, 0.1)'
                  }
                }}
                onClick={() => document.getElementById('audio-file').click()}
              >
                <CloudUpload sx={{ fontSize: 48, color: '#1DB954', mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  {file ? file.name : 'Click to select audio file'}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Supported formats: MP3, WAV, FLAC, AAC, OGG (Max 100MB)
                </Typography>
                <input
                  id="audio-file"
                  type="file"
                  accept="audio/*"
                  onChange={handleFileChange}
                  style={{ display: 'none' }}
                  disabled={uploading}
                />
              </Box>
            </CardContent>
          </Card>

          {/* Track Information */}
          <TextField
            fullWidth
            required
            label="Track Title"
            name="title"
            value={formData.title}
            onChange={handleChange}
            disabled={uploading}
            sx={{ mb: 3 }}
          />

          <TextField
            fullWidth
            required
            label="Artist"
            name="artist"
            value={formData.artist}
            onChange={handleChange}
            disabled={uploading}
            sx={{ mb: 3 }}
          />

          <TextField
            fullWidth
            label="Album"
            name="album"
            value={formData.album}
            onChange={handleChange}
            disabled={uploading}
            sx={{ mb: 3 }}
          />

          <TextField
            fullWidth
            label="Genre"
            name="genre"
            value={formData.genre}
            onChange={handleChange}
            disabled={uploading}
            sx={{ mb: 3 }}
          />

          <Button
            type="submit"
            variant="contained"
            size="large"
            disabled={uploading || !file}
            sx={{ mt: 2 }}
          >
            {uploading ? 'Uploading...' : 'Upload Track'}
          </Button>
        </Box>
      </Paper>
    </Container>
  );
};

export default Upload;
